const StyledTreeItemRoot = styled(TreeItem)(({ theme }) => ({
  color: theme.palette.text.secondary,
  [`& .${treeItemClasses.content}`]: {
    color: theme.palette.text.secondary,
    borderTopRightRadius: theme.spacing(2),
    borderBottomRightRadius: theme.spacing(2),
    paddingRight: theme.spacing(1),
    fontWeight: theme.typography.fontWeightMedium,
    "&.Mui-expanded": {
      fontWeight: theme.typography.fontWeightRegular,
    },
    "&:hover": {
      backgroundColor: theme.palette.action.hover,
    },
    "&.Mui-focused, &.Mui-selected, &.Mui-selected.Mui-focused": {
      backgroundColor: `var(--tree-view-bg-color, ${theme.palette.action.selected})`,
      color: "var(--tree-view-color)",
    },
    [`& .${treeItemClasses.label}`]: {
      fontWeight: "inherit",
      color: "inherit",
    },
  },
  [`& .${treeItemClasses.group}`]: {
    marginLeft: 0,
    [`& .${treeItemClasses.content}`]: {
      paddingLeft: theme.spacing(2),
    },
  },
}));

function StyledTreeItem(props) {
  const history = useHistory();
  const { bgColor, color, labelInfo, labelText, data, ...other } = props;
  const { filterState, setFilterState } = useContext(FilterContext);
  const handleSendToScenario = (itemName) => {
    setFilterState({
      ...filterState,
      // selectedFG: localStorage.getItem("SelectedParent"),
      itemToScroll: itemName,
    });
    history.push("/scenario-planner");
    console.log(itemName);
  };

  return (
    <StyledTreeItemRoot
      label={
        <Box
          id={data?.item === localStorage.getItem("PTNo") ? "itemToScroll" : ""}
          sx={{
            display: "flex",
            alignItems: "center",
            gap: "10px",
            // justifyContent: 'space-around',
          }}
        >
          <div
            style={{
              padding: "1px 5px",
              fontSize: "15px",
              fontWeight: "600",
              backgroundColor: `${
                data?.item === localStorage.getItem("PTNo")
                  ? "#0077B6"
                  : data?.childAltGrpRnk > 0
                  ? "#EA4335BF"
                  : "#9F9F9F"
              }`,
              color: "#fff",
              borderRadius: "5px",
              marginLeft: "10px",
            }}
          >
            {data?.Level}
          </div>
          <Typography
            variant="h6"
            sx={{
              fontWeight: "600",
              flex: 1.4,
              color: `${
                data?.item === localStorage.getItem("PTNo")
                  ? "#0077B6"
                  : data?.childAltGrpRnk > 0
                  ? "#EA4335BF"
                  : "inherit"
              }`,
            }}
          >
            <DescTooltip data={data?.description}>
              <span
                style={{
                  cursor: "pointer",
                }}
                onClick={() =>
                  data?.item === localStorage.getItem("PTNo")
                    ? handleSendToScenario(labelText)
                    : console.log("Item is not selected")
                }
              >
                {labelText}{" "}
                {Array.isArray(data?.child) &&
                  data?.child?.length > 0 &&
                  `(${data?.child?.length})`}
              </span>
            </DescTooltip>
          </Typography>
          <Typography
            variant="h6"
            sx={{
              fontWeight: "500",
              flex: 0.4,
              color: `${
                data?.item === localStorage.getItem("PTNo")
                  ? "#0077B6"
                  : data?.childAltGrpRnk > 0
                  ? "#EA4335BF"
                  : "inherit"
              }`,
            }}
          >
            {data?.QPA}
          </Typography>
          <Typography
            variant="h6"
            sx={{
              fontWeight: "500",
              flex: 0.5,
              color: `${
                data?.item === localStorage.getItem("PTNo")
                  ? "#0077B6"
                  : data?.childAltGrpRnk > 0
                  ? "#EA4335BF"
                  : "inherit"
              }`,
            }}
          >
            {data?.childAltGrp}
          </Typography>
          <Typography
            variant="h6"
            sx={{
              fontWeight: "500",
              flex: 0.6,
              color: `${
                data?.item === localStorage.getItem("PTNo")
                  ? "#0077B6"
                  : data?.childAltGrpRnk > 0
                  ? "#EA4335BF"
                  : "inherit"
              }`,
            }}
          >
            {data?.childAltGrpRnk}
          </Typography>
          <Typography
            variant="h6"
            sx={{
              fontWeight: "500",
              flex: 0.6,
              color: `${
                data?.item === localStorage.getItem("PTNo")
                  ? "#0077B6"
                  : data?.childAltGrpRnk > 0
                  ? "#EA4335BF"
                  : "inherit"
              }`,
            }}
          >
            {data?.childOper}
          </Typography>
          <Typography
            variant="h6"
            sx={{
              fontWeight: "500",
              flex: 0.5,
              color: `${
                data?.item === localStorage.getItem("PTNo")
                  ? "#0077B6"
                  : data?.childAltGrpRnk > 0
                  ? "#EA4335BF"
                  : "inherit"
              }`,
            }}
          >
            {data?.child_p_m_t}
          </Typography>
          <Typography
            variant="h6"
            sx={{
              fontWeight: "500",
              flex: 0.8,
              color: `${
                data?.item === localStorage.getItem("PTNo")
                  ? "#0077B6"
                  : data?.childAltGrpRnk > 0
                  ? "#EA4335BF"
                  : "inherit"
              }`,
            }}
          >
            {data?.item === localStorage.getItem("PTNo")
              ? `QOH: ${filterState?.SelectedPTQOH}`
              : ""}
          </Typography>
        </Box>
      }
      style={{
        "--tree-view-color": color,
        "--tree-view-bg-color": bgColor,
        paddingLeft: `${7 * data?.Level?.charAt(1)}px`,
      }}
      {...other}
    />
  );
}

const renderTree = (tree) => {
  return (
    <StyledTreeItem
      key={tree?.uuid + tree?.item + ""}
      nodeId={tree?.uuid + tree?.item + ""}
      labelText={tree?.item}
      data={tree}
    >
      {Array.isArray(tree?.child)
        ? tree?.child?.map((node) => renderTree(node))
        : null}
    </StyledTreeItem>
  );
};

const SubAssembly = () => {
  const { filterState, setFilterState } = useContext(FilterContext);
  const history = useHistory();
  const [searchValue, setSearchValue] = useState("");
  const [selectedParentName, setSelectedParentName] = useState("");
  const [ItemParentsData, setItemParentsData] = useState([]);
  const [subAssemblyData, setSubAssemblyData] = useState({});
  const [errorText, setErrorText] = useState(null);
  const [expanded, setExpanded] = useState([]);
  const [SelectedItemCount, setSelectedItemCount] = useState(0);

  useEffect(() => {
    if (localStorage.getItem("PTNo") === null) {
      setErrorText(
        "Please search Part No from above serach box or select from scenario page"
      );
      // history.push("/scenario-planner");
    } else {
      getSubAssemblyPart(localStorage.getItem("PTNo"));
    }
  }, [filterState?.location, filterState?.productCode]);

  const getSubAssemblyPart = (partNo) => {
    axios
      .post(
        `${BaseUrl}${filterState?.location}/subassembly/990-0288-140/${partNo}`,
        {
          productCode: filterState?.productCode,
        }
      )
      .then((res) => {
        // console.log(res.data);
        setItemParentsData(res?.data);
        setSelectedParentName(res?.data[0]?.Parentitem);
        localStorage.setItem("SelectedParent", res?.data[0]?.Parentitem);
        setErrorText("");
        localStorage.setItem("PTNo", partNo);
        getSubAssemblyItems(res?.data[0]?.Parentitem);
      })
      .catch((err) => {
        console.log(err);
      });
  };

  const getSubAssemblyItems = (fg) => {
    setExpanded([]);
    setSelectedItemCount(0);
    setSubAssemblyData({});
    axios
      .post(
        `${BaseUrl}${
          filterState?.location
        }/subassemblyV2/${fg}/${localStorage.getItem("PTNo")}`,
        {
          productCode: filterState?.productCode,
        }
      )
      .then(async (res) => {
        // console.log(res.data);
        setSubAssemblyData(res.data?.data);
        setFilterState({
          ...filterState,
          SelectedPTQOH: res?.data?.itemQOH,
        });
        await handleExpand(res.data?.data);
        await handleItemCount(res.data?.data);
      })
      .catch((err) => {
        console.log(err);
      });
  };

  const handleToggle = (event, nodeIds) => {
    setExpanded(nodeIds);
  };

  const handleResetClick = () => {
    setExpanded([]);
  };
  const testExpanded = [];
  const handleExpand = async (obj) => {
    testExpanded.push(obj?.uuid + obj?.item + "");
    obj?.child?.map(
      (childItem) =>
        childItem?.child?.length > 0 &&
        testExpanded.push(childItem?.uuid + childItem?.item + "")
    );
    setExpanded(testExpanded);
  };

  let count = 0;
  const handleItemCount = async (obj) => {
    if (obj?.item === localStorage.getItem("PTNo")) {
      count++;
    }
    if (obj?.child) {
      obj?.child?.map(handleItemCount);
    }
    setSelectedItemCount(count);
  };

  let firstNextClick = true;

  function scrollTo(direction) {
    if (localStorage.getItem("PTNo") !== null) {
      const elements = document.querySelectorAll("#itemToScroll");
      const numElements = elements.length;
      let currentIndex = -1;

      for (let i = 0; i < numElements; i++) {
        if (elements[i].getBoundingClientRect().top >= 0) {
          currentIndex = i;
          break;
        }
      }

      if (numElements === 1) {
        elements[0].scrollIntoView({ behavior: "smooth" });
      } else if (direction === "previous") {
        if (currentIndex > 0) {
          const previousElement = elements[currentIndex - 1];
          previousElement.scrollIntoView({ behavior: "smooth" });
        } else if (numElements > 1) {
          const lastElement = elements[numElements - 1];
          lastElement.scrollIntoView({ behavior: "smooth" });
        }
      } else if (direction === "next") {
        if (
          currentIndex !== -1 &&
          currentIndex < numElements - 1 &&
          firstNextClick
        ) {
          const nextElement = elements[currentIndex];
          nextElement.scrollIntoView({ behavior: "smooth" });
          firstNextClick = false;
        } else {
          const nextElement = elements[currentIndex + 1];
          nextElement.scrollIntoView({ behavior: "smooth" });
        }
      }
    }
  }

  return (
    <>
      <div
        style={{
          display: "flex",
          justifyContent: "space-around",
          backgroundColor: "#fff",
          position: "fixed",
          top: 64,
          left: 65,
          right: 0,
          zIndex: "9",
          padding: "15px 20px",
        }}
      >
        <div
          style={{
            flex: 2,
            display: "flex",
            alignItems: "center",
          }}
        >
          <Typography
            component="div"
            sx={{
              fontSize: "25px",
              color: "#0077B6",
              fontWeight: "600",
            }}
          >
            Sub Assembly - Part {localStorage.getItem("PTNo")} (
            {SelectedItemCount})
          </Typography>
          <div
            style={{
              display: "flex",
              gap: "10px",
              margin: "0px 20px",
              // border: "2px solid #0077B680",
              // borderRadius: "5px",
            }}
          >
            <IconButton
              onClick={() => scrollTo("previous")}
              type="button"
              sx={{ p: "0px" }}
              aria-label="search"
            >
              <KeyboardArrowUpIcon fontSize="large" sx={{ color: "#0077B6" }} />
            </IconButton>
            <IconButton
              onClick={() => scrollTo("next")}
              type="button"
              sx={{ p: "0px" }}
              aria-label="search"
            >
              <KeyboardArrowDownIcon
                fontSize="large"
                sx={{ color: "#0077B6" }}
              />
            </IconButton>
          </div>
          <div
            style={{
              fontSize: "14px",
              marginLeft: "20px",
              fontWeight: "600",
              marginTop: "5px",
            }}
          >
            Filter:{" "}
            {filterState?.filterName ? filterState?.filterName : "Custom"}
          </div>
        </div>
        <div
          style={{
            flex: 1,
            display: "flex",
            justifyContent: "flex-end",
            alignItems: "center",
          }}
        >
          <Paper
            sx={{
              p: "1px 2px",
              display: "flex",
              alignItems: "center",
              width: 250,
              border: "1px solid #E4E4E4",
              boxShadow: "none",
            }}
          >
            <InputBase
              sx={{ ml: 1, flex: 1 }}
              placeholder="PT Part No"
              inputProps={{ "aria-label": "PT Part No" }}
              value={searchValue}
              onChange={(e) => setSearchValue(e.target.value)}
            />
            <IconButton
              onClick={() => getSubAssemblyPart(searchValue)}
              type="button"
              sx={{ p: "5px" }}
              aria-label="search"
            >
              <SearchIcon />
            </IconButton>
          </Paper>
        </div>
      </div>
      <div style={{ display: "flex", minHeight: "75vh" }}>
        <div style={{ flex: 2, marginRight: "30px" }}>
          <div style={{ position: "fixed" }}>
            <Paper
              sx={{
                width: "100%",
                height: "auto",
                overflow: "hidden",
                boxShadow: "none",
                border: "1px solid #0077b6",
                borderRadius: "5px",
              }}
            >
              <TableContainer sx={{ maxHeight: "550px" }}>
                <Table stickyHeader size="small" aria-label="a dense table">
                  <TableHead>
                    <TableRow>
                      <TableCell
                        width="180px"
                        sx={{ fontWeight: "600", fontSize: "13px" }}
                      >
                        Parent Finished Good
                      </TableCell>
                      <TableCell
                        sx={{ fontWeight: "600", fontSize: "13px" }}
                        align="right"
                      >
                        QPA
                      </TableCell>
                      {/* <TableCell
                        sx={{ fontWeight: "600", fontSize: "13px" }}
                        align="right"
                      >
                        Level
                      </TableCell> */}
                    </TableRow>
                  </TableHead>
                  <TableBody>
                    {ItemParentsData.map((row, index) => (
                      <TableRow
                        key={index}
                        onClick={() => {
                          setSelectedParentName(row?.Parentitem);
                          localStorage.setItem(
                            "SelectedParent",
                            row?.Parentitem
                          );
                          getSubAssemblyItems(row?.Parentitem);
                        }}
                        sx={{
                          backgroundColor: `${
                            row?.Parentitem === selectedParentName
                              ? "#0077B6"
                              : "inherit"
                          }`,
                          cursor: "pointer",
                          "&:last-child td, &:last-child th": { border: 0 },
                        }}
                      >
                        <TableCell
                          sx={{
                            color: `${
                              row?.Parentitem === selectedParentName
                                ? "#fff"
                                : "inherit"
                            }`,
                          }}
                          component="th"
                          scope="row"
                        >
                          <DescTooltip data={row?.description}>
                            <span>{row?.Parentitem}</span>
                          </DescTooltip>
                        </TableCell>
                        <TableCell
                          sx={{
                            color: `${
                              row?.Parentitem === selectedParentName
                                ? "#fff"
                                : "inherit"
                            }`,
                          }}
                          align="right"
                        >
                          {row?.QPA}
                        </TableCell>
                        {/* <TableCell
                          sx={{
                            color: `${
                              row === selectedParentName ? "#fff" : "inherit"
                            }`,
                          }}
                          align="right"
                        ></TableCell> */}
                      </TableRow>
                    ))}
                  </TableBody>
                </Table>
                {ItemParentsData.length === 0 && (
                  <Stack spacing={1} sx={{ margin: "10px" }}>
                    <Skeleton variant="rounded" width="inherit" height={20} />
                    <Skeleton variant="rounded" width="inherit" height={20} />
                    <Skeleton variant="rounded" width="inherit" height={20} />
                    <Skeleton variant="rounded" width="inherit" height={20} />
                    <Skeleton variant="rounded" width="inherit" height={20} />
                    <Skeleton variant="rounded" width="inherit" height={20} />
                    <Skeleton variant="rounded" width="inherit" height={20} />
                    <Skeleton variant="rounded" width="inherit" height={20} />
                  </Stack>
                )}
              </TableContainer>
            </Paper>
          </div>
        </div>
        <div
          style={{
            flex: 8,
            position: "relative",
            overflow: "hidden",
            height: "calc(100vh - 160px)",
          }}
        >
          <Paper
            sx={{
              width: "inherit",
              padding: " 10px",
              overflow: "hidden",
              boxShadow: "none",
              borderRadius: "5px",
              position: "absolute",
              top: 0,
              left: 0,
              right: 0,
              zIndex: "99",
            }}
          >
            {errorText !== null && (
              <div
                style={{
                  color: "#EA4335",
                  fontSize: "15px",
                  fontWeight: "600",
                  padding: "10px",
                  backgroundColor: "#fff",
                  textAlign: "center",
                  borderRadius: "10px",
                }}
              >
                {errorText}
              </div>
            )}
            <div
              style={{
                display: "flex",
                backgroundColor: "#fff",
              }}
            >
              <div
                style={{
                  flex: 8,
                  display: "flex",
                  marginLeft: "40px",
                }}
              >
                <div
                  style={{
                    flex: 0.5,
                    display: "flex",
                    fontWeight: "600",
                    fontSize: "17px",
                  }}
                >
                  Level
                </div>
                <div
                  style={{
                    flex: 0.9,
                    display: "flex",
                    fontWeight: "600",
                    fontSize: "17px",
                  }}
                >
                  Item (Child count)
                </div>
                <div
                  style={{
                    flex: 0.4,
                    display: "flex",
                    fontWeight: "600",
                    fontSize: "17px",
                  }}
                >
                  QPA
                </div>
                <div
                  style={{
                    flex: 0.6,
                    display: "flex",
                    fontWeight: "600",
                    fontSize: "17px",
                  }}
                >
                  Alt_Group
                </div>
                <div
                  style={{
                    flex: 0.6,
                    display: "flex",
                    fontWeight: "600",
                    fontSize: "17px",
                  }}
                >
                  Alt_Grp_Rnk
                </div>
                <div
                  style={{
                    flex: 0.6,
                    display: "flex",
                    fontWeight: "600",
                    fontSize: "17px",
                  }}
                >
                  Oper_Num
                </div>
                <div
                  style={{
                    flex: 0.6,
                    display: "flex",
                    fontWeight: "600",
                    fontSize: "17px",
                  }}
                >
                  p_m_t
                </div>
              </div>
              <div style={{ flex: 1, display: "flex", justifyContent: "end" }}>
                <Button
                  variant="outlined"
                  sx={{ textTransform: "none", p: "2px 15px" }}
                  startIcon={<ReplayIcon />}
                  onClick={() => handleResetClick()}
                >
                  Reset
                </Button>
              </div>
            </div>
          </Paper>
          <Paper
            sx={{
              width: "100%",
              padding: " 15px 20px",
              boxShadow: "0px 3px 6px #0000001F",
              borderRadius: "5px",
              position: "absolute",
              left: 0,
              top: "70px",
              width: "100%",
              height: "calc(100% - 70px)",
              overflowY: "scroll",
            }}
          >
            <TreeView
              disableSelection
              aria-label="file system navigator"
              defaultCollapseIcon={<KeyboardArrowDownIcon />}
              defaultExpandIcon={<KeyboardArrowRightIcon />}
              expanded={expanded}
              onNodeToggle={handleToggle}
              sx={{
                overflowY: "auto",
                flexGrow: 1,
                // maxWidth: 800,
              }}
            >
              {renderTree(subAssemblyData)}
            </TreeView>
          </Paper>
        </div>
      </div>
    </>
  );
};

export default SubAssembly;
